import { formatDate } from '@/libs/moment';
export const columns = [
    {
        title: '项目名称',
        width: 180,
        minWidth: 120,
        name: 'name',
    },
    {
        title: '项目描述',
        minWidth: 150,
        name: 'desc',
    },
    {
        title: '更新日志',
        minWidth: 150,
        name: 'log',
        formatter: val => {
            return val === '' ? '暂无' : val
        }
    },
    {
        title: '版本',
        width: 120,
        minWidth: 100,
        name: 'version',
        formatter: val => {
            return val === '' ? '未发布' : val
        }
    },
    {
        title: '下载次数',
        width: 120,
        name: 'download'
    },
    {
        title: '创建时间',
        width: 160,
        name: 'created_at',
        formatter: val => formatDate(val)
    },
    {
        title: '更新时间',
        width: 160,
        name: 'updated_at',
        formatter: val => formatDate(val)
    }
];
