<template>
    <div class="project">
        <div class="actions">
            <el-button @click="addItem" type="success" icon="el-icon-folder-add">添加项目</el-button>
            <DataFilter @filter="filterData"/>
        </div>
        <el-table class="full-width" :data="rows" v-loading="loading" stripe border>
            <el-table-column
                v-for="(column, index) in columns"
                :key="index"
                :prop="column.name"
                :label="column.title"
                :width="column.width"
                :show-overflow-tooltip="true"
                sortable
            >
                <template slot-scope="scope">
                    {{ column.formatter ? column.formatter(scope.row[column.name]) : scope.row[column.name] }}
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="220" width="250" fixed="right">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" title="版本更新" @click="publish(scope.row)" icon="el-icon-upload"/>
                    <el-button size="mini" type="warning" title="项目编辑" @click="editItem(scope.row)" icon="el-icon-edit"/>
                    <el-button size="mini" title="下载资源" type="success" @click="downloadItem(scope.row)" icon="el-icon-download"/>
                    <el-button size="mini" type="danger" title="项目删除" @click="removeItem(scope.row)" icon="el-icon-delete"/>
                </template>
            </el-table-column>
        </el-table>
        <div class="paging padding">
            <el-pagination background layout="prev, pager, next" :total="filterList.length" :current-page.sync="currentPage" :hide-on-single-page="true"/>
        </div>
        <el-dialog :title="dialogTitle" :visible.sync="addFileDialog" width="450px" :modal-append-to-body="false" @closed="cancel($event, 'form')">
            <el-form :model="proj" :rules="rules" ref="form" label-width="100px">
                <el-form-item prop="name" label="项目名称">
                    <el-input v-model="proj.name" placeholder="请输项目名称"/>
                </el-form-item>
                <el-form-item prop="desc" label="项目描述">
                    <el-input  type="textarea" v-model="proj.desc" placeholder="请输入项目描述" :rows="5" maxlength="400" show-word-limit/>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button size="small" @click="addFileDialog = false">取消</el-button>
                <el-button type="primary" size="small" @click="submit('form')">确定</el-button>
            </div>
        </el-dialog>
        <el-dialog :title="dialogTitle" :visible.sync="upgradeDialog" width="600px" :modal-append-to-body="false" @closed="cancel($event, 'upgrade')">
            <el-form :model="upgrade" :rules="rules" ref="upgrade" label-width="100px">
                <el-form-item prop="version" label="项目版本">
                    <el-input v-model="upgrade.version" :placeholder="'当前版本' + tmp.version"/>
                </el-form-item>
                <el-form-item prop="rh_ids" label="绑定资源">
                    <el-row type="flex" justify="space-between">
                        <el-col :span="7">
                            <el-select v-model="selected.alias" @change="listResource" placeholder="请选择分类">
                                <el-option v-for="(item, index) in aliasList" :key="index" :label="item.name" :value="item.id"/>
                            </el-select>
                        </el-col>
                        <el-col :span="7">
                            <el-select v-model="selected.id" @change="listHistory" placeholder="请选择资源">
                                <el-option v-for="(item, index) in resourcesList" :key="index" :label="item.name" :value="item.id"/>
                            </el-select>
                        </el-col>
                        <el-col :span="9">
                            <el-select v-model="selected.version" collapse-tags @change="pickItem" placeholder="请选择版本">
                                <el-option v-for="(item, index) in versions" :disabled="item.disabled" :key="index" :label="item.version" :value="item.id"/>
                            </el-select>
                        </el-col>
                    </el-row>
                    <div class="selected">
                        <template v-if="pickedItems.length > 0">
                            <el-tooltip v-for="item in pickedItems" :key="item.id" :content="'更新日志：' + (item.log || '暂无')" placement="right-start">
                                <el-tag closable @close="removePickedItem(item.id)">
                                    {{ item.name }} {{ item.version }}
                                </el-tag></el-tooltip>
                        </template>
                        <p :style="{
                            color: '#aaa'
                        }" v-else>暂无绑定的资源</p>
                    </div>
                </el-form-item>
                <el-form-item prop="log" label="更新日志">
                    <el-input type="textarea" v-model="upgrade.log" :placeholder="tmp.log"/>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button size="small" @click="upgradeDialog = false">取消</el-button>
                <el-button type="primary" size="small" :loading="uploadProcessing" @click="submit('upgrade')">{{ uploadProcessing ? '提交中' : '确定' }}</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as http from '@/libs/request';
    import { getToken } from '@/libs/auth';
    import { ObjectToFormdata } from '@/libs/formdata';
    import { columns } from '@/mixins/Project';
    import DataFilter from '@/components/datafilter';

    var projVersion = '0.0.0';

    export default {
        data: () => ({
            columns,
            list: [],
            filterList: [],
            currentPage: 1,
            aliasList: [],
            resourcesList: [],
            history: [],
            selected: {
                alias: '',
                id: '',
                version: '',
            },
            isEdit: false,
            dialogTitle: '',
            addFileDialog: false,
            file: {
                id: '',
                name: '',
                desc: '',
            },
            proj: {
                name: '',
                desc: '',
            },
            rules: {
                name: [
                    {required: true, message: '项目名称不能为空', trigger: 'blur'}
                ],
                desc: [
                    {required: true, message: '描述不能为空', trigger: 'blur'}
                ],
                version: [
                    {required: true, message: '资源版本不能为空', trigger: 'blur'},
                    {
                        validator: (rule, value, callback) => {
                            let reg = /^\d+\.\d+\.\d+$/;
                            if (!reg.test(value)) {
                                callback(new Error('请输入合法的版本号, 例如 x.x.x '))
                            } else {
                                callback()
                            }
                        }, trigger: 'blur'
                    },
                    {
                        validator: (rule, value, callback) => {
                            let [u, v, w] = value.split('.');
                            let [x, y, z] = projVersion.split('.');
                            let p = v => parseInt(v);
                            if(p(u) > p(x) || (p(u) === p(x) && p(v) > p(y)) || (p(u) === p(x) && p(v) === p(y) && p(w) > p(z))) {
                                callback()
                            }
                            else {
                                callback(new Error('发布的版本号不能低于当前版本号, 当前版本 ' + projVersion))
                            }
                        }, trigger: 'blur'
                    }
                ],
                rh_ids: [
                    {required: true, message: '需要至少选择一个资源', trigger: 'blur'}
                ]
            },
            uploadProcessing: false,
            loading: false,
            upgrade: {
                project_id: '',
                version: '',
                rh_ids: '',
                log: '',
            },
            tmp: {
                version: '',
                log: '',
            },
            pickedItems: [],
            upgradeDialog: false,
        }),
        components: { DataFilter },
        methods: {
            listProjects() {
                this.loading = true;
                http.listProjects({
                    success: res => {
                        this.filterList = this.list = res || [];
                        this.loading = false;
                    },
                    fail: err => {
                        this.$message.error('获取项目列表失败, ' + err);
                        this.loading = false;
                    }
                })
            },
            listAlias() {
                http.listAlias({
                    success: res => {
                        this.aliasList = res.resource_types ? res.resource_types.map(item => {
                            return {
                                id: item.id,
                                name: item.name,
                            }
                        }) : [];
                    },
                    fail: err => {
                        this.$message.error('获取资源分类失败, ' + err)
                    }
                })
            },
            listResource(alias) {
                this.selected.id = '';
                this.selected.version = '';
                http.listResource({resource_type: alias}, {
                    success: res => {
                        this.resourcesList = res ? res.map(({id, name}) => {
                            return {
                                id,
                                name,
                            }
                        }) : [];
                    },
                    fail: err => {
                        this.$message.error('获取资源列表失败, ' + err);
                    }
                })
            },
            listHistory(id) {
                this.selected.version = '';
                http.listHistory({resource_id: id}, {
                    success: res => {
                        this.history = res.resources;
                    },
                    fail: err => {
                        this.$message.error(err)
                    }
                })
            },
            projectDetail(id) {
                const callback = {
                    success: res => {
                        this.pickedItems = res.map(({ id, file, version, log }) => {
                            return {
                                id,
                                name: file,
                                version,
                                log,
                            }
                        })
                    },
                    fail: err => {
                        this.$message.error(err)
                    }
                }
                http.projectDetail({ id }, callback)
            },
            addItem() {
                this.isEdit = false;
                this.proj = {};
                this.dialogTitle = "添加项目";
                this.addFileDialog = true;
            },
            publish(row) {
                this.upgrade.project_id = row.id;
                this.projectDetail(row.id);
                this.dialogTitle = "更新版本";
                this.tmp.version = row.version;
                this.tmp.log = row.log;
                projVersion = row.version;
                this.listAlias();
                this.upgradeDialog = true;
            },
            editItem(row) {
                this.isEdit = true;
                this.dialogTitle = "编辑项目";
                this.file = {...row};
                this.proj = {id: row.id, name: row.name, desc: row.desc};
                this.addFileDialog = true;
            },
            cancel(ev, form) {
                this.addFileDialog = false;
                this.upgradeDialog = false;
                this.reset(form);
                this.selected = {
                    alias: '',
                    id: '',
                };
                this.pickedItems = [];
                projVersion = '0.0.0';
            },
            reset(form) {
                this.file = {
                    name: '',
                    desc: '',
                }
                this.$refs[form].resetFields();
            },
            submit(form) {
                this.$refs[form].validate(valid => {
                    if (!valid) {
                        return false;
                    } else {
                        if (form === 'form') {
                            this.saveFile()
                        } else if (form === 'upgrade') {
                            this.publishProject()
                        }
                    }
                })
            },
            saveFile() {
                let callback = {
                    success: res => {
                        this.addFileDialog = false;
                        this.listProjects();
                    },
                    fail: err => {
                        this.$message.error('操作失败, ' + err)
                    }
                };
                let formData = ObjectToFormdata(this.proj);
                if (this.isEdit) {
                    http.updateProject(formData, callback)
                } else {
                    http.addProject(formData, callback)
                }
            },
            publishProject() {
                let callback = {
                    success: res => {
                        this.uploadProcessing = false;
                        this.upgradeDialog = false;
                        this.listProjects();
                    },
                    fail: err => {
                        this.uploadProcessing = false;
                        this.$message.error('操作失败, ' + err)
                    }
                };
                this.uploadProcessing = true;
                let req = {...this.upgrade};
                let formData = ObjectToFormdata(req);
                http.upgradeProject(formData, callback);
            },
            removeItem(row) {
                this.$confirm('此操作将永久删除该项目, 是否继续?', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.deleteProject(row.id)
                })
            },
            deleteProject(id) {
                http.deleteProject({
                    id
                }, {
                    success: res => {
                        this.listProjects()
                    },
                    fail: err => {
                        this.$message.error('获取资源失败, ' + err)
                    }
                })
            },
            pickItem(ev) {
                let pickedResource = this.resourcesList.filter(d => d.id === this.selected.id);
                let pickedVersion = this.history.filter(d => d.id === ev);
                this.pickedItems.push({
                    id: ev,
                    name: pickedResource[0].name,
                    version: pickedVersion[0].version,
                    log: pickedVersion[0].log,
                });
                this.selected.version = '';
            },
            removePickedItem(id) {
                this.pickedItems = this.pickedItems.filter(d => d.id !== id);
            },
            filterData({ type, keyword }) {
                let _keyword = keyword.toLocaleLowerCase();
                this.filterList = this.list.filter(d => d[type === 0 ? 'name' : 'desc'].toLocaleLowerCase().indexOf(_keyword) > -1)
            },
            downloadItem({ id, version, file }) {
                let link = document.createElement('a');
                link.href = http.getBaseUrl() + 'project/download?id=' + id + '&version=' + version + '&token=' + getToken();
                link.download = file;
                link.click();
            }
        },
        computed: {
            versions() {
                return this.history.map(d => {
                    let disabled = this.pickedItems.filter(s => s.id === d.id);
                    if (this.resourcesList === []) {
                        return {}
                    }
                    return {
                        ...d,
                        disabled: disabled.length > 0
                    }
                })
            },
            rows() {
                let limit = (this.currentPage - 1) * 10;
                return this.filterList.slice(limit, limit + 10);
            }
        },
        watch: {
            pickedItems(list) {
                this.upgrade.rh_ids = list.map(d => d.id).join(',');
            }
        },
        mounted() {
            this.listProjects();
        },
    }
</script>

<style scoped>
    .actions {
        display: flex;
        justify-content: space-between;
    }
    .paging {
        display: flex;
        justify-content: center;
    }

    .file-field {
        height: 0;
        overflow: hidden;
    }

    .selected {
        margin-top: 5px;
    }

    .selected .el-tag {
        margin-right: 10px;
    }
</style>
